body{
  /* background-image: url(assets/bg.jpg); */
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: absolute;
  background-position-x: 50%;
  background-position-y:-160% ;
  background-color: rgb(243, 237, 227);
}
